const piTo10000 =
  "";
const dayjs = require("dayjs");

export const formatDate = (date) => {
  const getDate = dayjs(date);
  const day = Number(getDate.$D);
  const month = Number(getDate.$M) + 1;
  const year = Number(getDate.$y);

  return `${day}/${month}/${year}`;
};

export const greyObj = {
  0: "#FFFFFF",
  1: "#E5E5E5",
  2: "#CDCDCD",
  3: "#B3B3B3",
  4: "#9A9A9A",
  5: "#808080",
  6: "#666666",
  7: "#4D4D4D",
  8: "#323232",
  9: "#181818",
};

export const piToColour = (num, colourObj) => {
    let hexArray = [];
    const piArray = piTo10000.split("").splice(0, num);
    for (let i = 1; i < num+1; i++) {
      const piNum = piArray[i-1];
      const piPair = [i, colourObj[piNum]];
      hexArray.push(piPair)
    }
    return hexArray;
};
